#background {
  width: 100%;
  height: 100%;
  background: black;
  position: absolute;
  text-align: -webkit-center;
  text-align: -moz-center;
}


#Wavy,
#Wavy2 {
  width: 500px;
  margin-top: 40px;
  filter: -webkit-grayscale(50%);
  filter: grayscale(50%);
  position: absolute;
}

#Wavy2 {
  opacity: 0.3;
  margin-left: -18px;
  margin-top: 45px;
  filter: -webkit-saturate(4);
  filter: saturate(4);
  z-index: 1000;
}

svg {
  margin-right: 15px;
  font-size: 20px;
  cursor: pointer;
}

#imageCover {
  width: 60%;
  position: absolute;
  text-align: -webkit-center;
  margin-top: 20px;
}

div#topBar {
  width: 100%;
  color: white;
  height: 40px;
  float: left;
}


p#logo {
  float: left;
  margin: 0px;
  margin-left: 15px;
  font-size: 26px;
  font-style: italic;
  padding: 0px;
  margin-top: 5px;
}

#icons {
  float: right;
  margin-right: 15px;
}


#footer {
  width: 100%;
  height: 30px;
  position: absolute;
  background: rgb(12, 12, 12);
  bottom: 0;
  color: white;
}

#sb{
  float: left;
  margin: 0px;
  margin-top: 5px;
  margin-left: 15px;
  font-size: 13px;
  opacity: 0.5;
}


#copyright{
  float: right;
  margin: 0px;
  margin-top: 5px;
  margin-right: 15px;
  font-size: 13px;
  opacity: 0.5;
}

@media (max-width:1000px) {
  #imageCover {
    width: 32%;
  }

  #Wavy,
  #Wavy2 {
    width: 250px;
  }
}